//Same functionality for other modals


const aboutMenuClose = document.querySelector(".full-screen-about .full-screen-menu-close");

//other modal selectors
const aboutMeModal = document.querySelector(".about-modal-toggle");

const toggleAboutMeModal = () => {
  const fullModal = document.querySelector(".full-screen-about");
  fullModal.classList.toggle("active");
  document.querySelector("section").classList.toggle("blurry-background");
}

aboutMeModal.addEventListener("click", toggleAboutMeModal);
aboutMenuClose.addEventListener("click", toggleAboutMeModal);




/* About Section */
const aboutTags = Array.from(document.querySelectorAll(".about-tag"));
const professionalTags = Array.from(document.querySelectorAll(".professional-tag"));

var tagInitialStateTop = [];
var tagInitialStateLeft = [];


//Refactor DRY 

//Personal-container
//Mouseenter function
document.querySelector(".about-personal-container").addEventListener("mouseenter", function(){
  
  //Store the initial position
    const initialTagPosition = aboutTags.forEach((tags) =>{
      tagInitialStateTop.push(window.getComputedStyle(tags).getPropertyValue('top'));
      tagInitialStateLeft.push(window.getComputedStyle(tags).getPropertyValue('left'));
    });
  
  const filteredTags = aboutTags.filter(tag => tag.classList.contains("personal-tag"));

  const applyTagPosition = filteredTags.forEach((tags) =>{
      const tagPosition = 'calc('+window.getComputedStyle(tags).getPropertyValue('top')+' + 15%)';
      
      tags.setAttribute("style", "left: 19%; color: white; top:" + tagPosition)
    })
})

//Mouseleave function
document.querySelector(".about-personal-container").addEventListener("mouseleave", function(){
  
  //empty the Arrays
  tagInitialStateTop = [];
  tagInitialStateLeft = [];
  
  const filteredTags = aboutTags.filter(tag => tag.classList.contains("personal-tag"))

  const applyTagPosition = filteredTags.forEach((tags, index) =>{
      tags.setAttribute("style", "top:" + tagInitialStateTop[index] + ";" + "left:" + tagInitialStateLeft[index] + ";" )
  })
})


//Profesional container
//Mouseenter function
document.querySelector(".about-professional-container").addEventListener("mouseenter", function(){
  
  //Store the initial position
    const initialTagPosition = professionalTags.forEach((tags) =>{
      tagInitialStateTop.push(window.getComputedStyle(tags).getPropertyValue('top'));
      tagInitialStateLeft.push(window.getComputedStyle(tags).getPropertyValue('left'));
    })
  
  const filteredTags = professionalTags.filter(tag => tag.classList.contains("professional-tag"))

  const applyTagPosition = filteredTags.forEach((tags) =>{
      const tagPosition = 'calc('+window.getComputedStyle(tags).getPropertyValue('top')+' + 15%)';
      
      tags.setAttribute("style", "left: 68%; color: white; top:" + tagPosition)
    });
});

//Mouseleave function
document.querySelector(".about-professional-container").addEventListener("mouseleave", function(){

   //empty the Arrays
  tagInitialStateTop = [];
  tagInitialStateLeft = [];
  
  const filteredTags = professionalTags.filter(tag => tag.classList.contains("professional-tag"))

  const applyTagPosition = filteredTags.forEach((tags, index) =>{
      tags.setAttribute("style", "top:" + tagInitialStateTop[index] + ";" + "left:" + tagInitialStateLeft[index] + ";")
  });
});

